* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    color: black;
}

section {
    position: -webkit-sticky; /* Required for Safari */
    position: sticky;
    height: auto;
    top: 0;
    overflow: visible;
    padding: 0;
    margin: 0;
    
}

body {
    height: 100vh;
    margin: 0;
    font-family: 'cursive', 'Arial Narrow', Arial, sans-serif;
    background-image: url(https://images.unsplash.com/photo-1583087750047-74631ab2ab47?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=870&q=80);
    background-position: center;
    background-position-y: top;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
}

a {
    text-decoration: none;
}

li {
    list-style: none;
}

/*Nav Styling Starts*/
.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    /*Transparent Background color with rgba hack (0,0,0,.5) gives transparent effect*/
    background-color: rgb(255, 255, 255);
    color: rgba(42, 212, 255, 0.459);
    position: fixed;
    z-index: +1;
    width: 100%;
}

.nav-links a {
    color: rgb(0, 183, 255);
    font-size: larger;
    font-family: monospace;
    font-weight: 800;
}

/* LOGO */
.logo {
    position: fixed;
    left: 0px;
    height: auto;
    width: 60px;
    border-radius: 50%;
    top: 5px;
    margin-left: 15px;
    z-index: -1;
    transition: .3s ease ;
    
}

.logo2 {
    position: fixed;
    left: 60px;
    height: auto;
    width: 170px;
    border-radius: 50%;
    top: 5px;
    margin-left: 15px;
    z-index: -1;
    transition: .3s ease ;
    
}




/* Navbar Menu */
.menu {
    display: flex;
    gap: 1em;
    font-size: 18px;
}

.menu li:hover {
    background-color: #000000;
    border-radius: 5px;
    transition: 0.3s ease;
}

.menu li {
    padding: 5px 14px;
}

/* Dropdown Menu */
.services {
    position: relative;
}

.dropdown {
    background-color: rgb(0, 0, 0);
    padding: 1em 0;
    position: absolute; /* With Respect To Parent */
    display: none;
    border-radius: 8px;
    top: 35px;
}

.dropdown li + li {
    margin-top: 10px;
}

.dropdown li {
    padding: 0.5em 1em;
    width: 8em;
    text-align: center;
}

.dropdown li:hover {
    background-color: #ffffff;
}

.services:hover .dropdown {
    display: block;
}
/*Nav Bar Styling Ends*/

/*RESPONSIVE NAVBAR MENU STARTS*/
/* CHECKBOX HACK */
input[type=checkbox] {
    display: none;
}

/*Hamburger Menu*/
.hamburger {
    display: none;
    font-size: 24px;
    user-select: none;
}

/*Applying Media Queries*/
@media (max-width: 768px) {
    .menu {
        display: none;
        position: absolute;
        background-color: teal;
        right: 0;
        left: 0;
        text-align: center;
        padding: 16px 0;
    }

    .menu li:hover {
        display: inline-block;
        background-color: #000000;
        transition: 0.3s ease;
    }

    .menu li + li {
        margin-top: 12px;
    }

    input[type=checkbox]:checked ~ .menu{
        display: block;
    }

    .hamburger {
        display: block;
    }

    .dropdown {
        left: 50%;
        top: 30px;
        transform: translateX(35%);
    }

    .dropdown li:hover {
        background-color: #000000;
    }
}
/*RESPONSIVE NAVBAR MENU ENDS*/

